import { createRouter, createWebHistory } from 'vue-router'

import MapCity from './views/MapCityView.vue'

import ProfilePopup from './views/ProfileMenu/ProfilePopupView.vue'
import ProfileInfo from './views/ProfileMenu/ProfileInfoView.vue'
import ProfileSettings from './views/ProfileMenu/ProfileSettingsView.vue'
import Wallet from './views/ProfileMenu/WalletView.vue'
import Reward from './views/ProfileMenu/RewardView.vue'
import Asset from './views/AssetView.vue'
import AssetBuy from './views/AssetBuyView.vue'


const routes = [
    {
        path: '/',
        redirect: '/map-city'
    },
    {
        path: '/map-city',
        name: 'map-city',
        component: MapCity,
        children: [
            {
                path: 'profile',
                component: ProfilePopup
            },
            {
                path: 'profile-settings',
                component: ProfileSettings
            },
            {
                path: 'profile-info',
                component: ProfileInfo
            },
            {
                path: 'reward',
                component: Reward
            },
            {
                path: 'wallet',
                component: Wallet
            },
            {
                path: 'asset',
                component: Asset
            },
            {
                path: 'asset-buy',
                component: AssetBuy
            }
        ]
    }
]

const router = createRouter({
    history: createWebHistory(process.env.BASE_URL),
    routes,
})
  
export default router