<template>
    <div class="flex justify-end w-full h-screen" :class="this.leftSidebar ? 'filter-expanded' : ''">
        <SidebarComponent
            class="relative z-10 w-full h-screen overflow-y-auto bg-black-10 lg:w-1/2 lg:transition-all custom-scroll"
            v-if="this.leftSidebar" :pageView="this.pageView" v-click-outside="closeSidebar" />
        <router-view />
        <div class="relative ml-auto duration-75 ease-in-out lg:transition-all shrink-0"
            :class="this.leftSidebar ? 'hidden lg:block lg:w-1/2 lg:rounded-l-[80px] lg:overflow-hidden' : 'w-full'">
            <!-- Map Will Goes Here -->
            <div id="map" class="w-full h-screen" :class="this.leftSidebar ? 'pointer-events-none' : ''" style="border:0;">
               
            </div>
            <MapControlsComponent :mapZoom="this.mapZoom"/>
        </div>
    </div>
</template>

<script>
import mapboxgl from 'mapbox-gl'

import MapControlsComponent from '../components/MapControlsComponent.vue';
import SidebarComponent from '../components/SidebarComponent.vue'

export default {
    name: 'MapCityView',
    data() {
        return {
            leftSidebar: false,
            pageView: "",
            map: null,
            mapZoom: 11
        }
    },
    components: { SidebarComponent, MapControlsComponent },
    mounted() {
        mapboxgl.accessToken = 'pk.eyJ1IjoiZWFydGhtZXRhIiwiYSI6ImNreTRwMTNsNDBlM2syb21uMHN6N2N6cGIifQ.AYjxQVHXzFha47d9z3ph3A';
        this.map = new mapboxgl.Map({
            container: 'map',
            style: 'mapbox://styles/mapbox/streets-v12',
            zoom: 11,
            center: [30, 50],
            projection: 'globe',
            mapZoom: 11
        });
        this.map.on('zoom', () => {
            this.mapZoom = this.map.getZoom();
        })
    },
    methods: {
        openSidebar(pageView) {
            this.leftSidebar = true;
            this.pageView = pageView;
        },
        closeSidebar() {
            this.leftSidebar = false;
            this.pageView = "";
        },
        updateZoomValue(zoomValue) {
            this.map.zoomTo(zoomValue * (22 / 100));
            //this.map.jumpTo({zoom: (zoomValue * (22 / 100))})
        },
        resetNorth(){
            this.map.flyTo({bearing: 0, pitch: 0})
        }
    },
}
</script>