            this.loggedIn = false;
        },
        closeProfileMenu() {
            this.showProfileMenu = false
        },
        closeProfileMenuMobile() {
            this.showProfileMenuMobile = false
        },
        zoomIn(){
            this.zoomValue = this.zoomValue + 4.5 > 100  ? 100 : this.zoomValue + 4.5;
            this.$parent.updateZoomValue(this.zoomValue);
        },
        zoomOut(){
            this.zoomValue = this.zoomValue - 4.5 < 4.5 ? 4.5 : this.zoomValue - 4.5;
            this.$parent.updateZoomValue(this.zoomValue);
            
        },
        defaultZoom(){
            this.zoomValue = 50;
            this.$parent.updateZoomValue(this.zoomValue);
        }
    },
    watch: {
        mapZoom() {
            this.zoomValue = this.mapZoom * (100 / 22)
        }
    },
    components: { ProfileMenuComponent },
    props: ['map', 'mapZoom'],
};
</script>
