<template>
  <fullscreen>
    <router-view />
  </fullscreen>
</template>

<script>

import { ref, toRefs, reactive } from 'vue'

export default {
    name: 'App',
    methods: {
      toggleApi() {
        this.$fullscreen.toggle()
      }
    },
    setup() {
      const root = ref()
      const state = reactive({
        fullscreen: false,
      })
      function toggle() {
        state.fullscreen = !state.fullscreen
      }
      return {
        root,
        ...toRefs(state),
        toggle
      }
    }
  }
</script>